<template>
  <div class="StudentWork">
      <el-card>
        <el-main>
          <el-table :data="tableData" v-loading="loading" border>
            <el-table-column prop="id" label="用户ID" align="center"></el-table-column>
            <el-table-column prop="userId" label="所属班级" align="center"></el-table-column>
            <el-table-column prop="title" label="作业名称" align="center"></el-table-column>
            <el-table-column prop="completed_text" label="完成情况" align="center"></el-table-column>
          </el-table>
        </el-main>
        <el-footer style="height: 10%;">
          <el-card>
            <el-pagination
                @size-change="handleSizeChange"
                @current-change="handleCurrentChange"
                :current-page="page"
                :page-sizes="[5, 10, 20, 30,50]"
                :page-size="size"
                layout="total, sizes, prev, pager, next, jumper"
                :total="total">
            </el-pagination>
          </el-card>
        </el-footer>
      </el-card>
  </div>
</template>

<script>
export default {
  name: "StudentWork",
  data(){
    return{
      tableData:[],
      total:0,
      page:1,
      size:10,
      loading:true,
      handleSizeChange:'',
      handleCurrentChange:'',
    }
  },
  created() {
    this.total = this.tableData.length;
    let that = this;
    setTimeout(() => {
      that.$message({message:'加载完成暂无数据',type:'warning'})
      that.loading=false
    }, 1500);
  }
}
</script>

<style scoped lang="scss">
.StudentWork {
  width: 100%;
  height: 100%;
  .el-card{
    width: 100%;
    height: 100%;
    ::v-deep.el-card__body{
      width: 100%;
      height: 100%;
      padding: 0;
      .el-main{
        width: 100%;
        height: 85%;
        .el-table{
          width: 100%;
          height: 100%;
          .el-table__header-wrapper{
            height: 15%;
          }
          .el-table__body-wrapper{
            overflow: overlay;
            position: relative;
            height: 85%;
          }
        }
      }
      .el-footer{
        height: 10%;
        width: 100%;
        margin: 5px 0;
        .el-pagination{
          padding: 1%;
        }
      }
    }
  }
}

</style>